/* ============= Animation background ========= */
.PostSection_background__2yC37 {
    background-size: 400% 400%;
    -webkit-animation: PostSection_Gradient__2p-k1 15s ease infinite;
            animation: PostSection_Gradient__2p-k1 15s ease infinite;
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    padding:0;
    margin:0px;
}
#PostSection_form__3zCoK{

}

.PostSection_cube__150Aw {
    position: absolute;
    top: 80vh;
    left: 45vw;
    width: 10px;
    height: 10px;
    border: solid 1px #515BF4;
    -webkit-transform-origin: top left;
            transform-origin: top left;
    -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
            transform: scale(0) rotate(0deg) translate(-50%, -50%);
    -webkit-animation: PostSection_cube__150Aw 12s ease-in forwards infinite;
            animation: PostSection_cube__150Aw 12s ease-in forwards infinite;
}
.PostSection_cube__150Aw:nth-child(2n) div {
    border-color: #FFF ;
}
.PostSection_cube__150Aw:nth-child(2) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    left: 25vw;
    top: 40vh;
}
.PostSection_cube__150Aw:nth-child(3) {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    left: 75vw;
    top: 50vh;
}
.PostSection_cube__150Aw:nth-child(4) {
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
    left: 90vw;
    top: 10vh;
}
.PostSection_cube__150Aw:nth-child(5) {
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
    left: 10vw;
    top: 85vh;
}
.PostSection_cube__150Aw:nth-child(6) {
    -webkit-animation-delay: 5s;
            animation-delay: 5s;
    left: 50vw;
    top: 10vh;
}

/* Animate Background*/
@-webkit-keyframes PostSection_Gradient__2p-k1 {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}
@keyframes PostSection_Gradient__2p-k1 {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}
@-webkit-keyframes PostSection_cube__150Aw {
    from {
        -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
                transform: scale(0) rotate(0deg) translate(-50%, -50%);
        opacity: 1;
    }
    to {
        -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
                transform: scale(20) rotate(960deg) translate(-50%, -50%);
        opacity: 0;
    }
}
@keyframes PostSection_cube__150Aw {
    from {
        -webkit-transform: scale(0) rotate(0deg) translate(-50%, -50%);
                transform: scale(0) rotate(0deg) translate(-50%, -50%);
        opacity: 1;
    }
    to {
        -webkit-transform: scale(20) rotate(960deg) translate(-50%, -50%);
                transform: scale(20) rotate(960deg) translate(-50%, -50%);
        opacity: 0;
    }
}
